import styled from "@emotion/styled";
import axios from "axios";
import { useCallback, useState } from "react";
import {
  Button,
  Paper,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
  TextField
} from "@mui/material";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

import "./App.css";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  padding: 16px;

  .section-accordion {
    background-color: gray;
    margin: 4px 0;
    border-radius: 8px;

    .AccordionDetails {
    }
  }
`;

const HandRecord = styled.div`
  display: flex;
  flex-direction: column;

  background-color: lightgray;
  margin: 4px 0;
  padding: 8px;
  border-radius: 8px;

  & > p {
    margin: 1px 0;
  }

  .bold {
    font-weight: 800;
  }
`;

const InputWrap = styled.div`
  display: flex;
  flex-direction: row;

  align-items: center;
  margin: 16px 0;

  & > div {
    margin-right: 8px;
  }
`

const scrape = async (id) => {
  const { data } = await axios.get(`http://bridge.hr/api/tournaments/${id}`);
  const handRecords = data.data.handRecords;

  return handRecords;
};

function App() {
  const [data, setData] = useState({
    records: {},
    sessions: []
  });

  const [tournamentId, setTournamentId] = useState("")


  const handleChange = (e) => {
    setTournamentId(e.target.value);
  }

  const fetch = useCallback(() => {
    scrape(tournamentId).then((results) => {
      const handResultsPerSession = results.reduce((records, handRecord) => {
        const sessionId = handRecord.session_id.toString();
        if (sessionId) {
          if (!records[sessionId]) {
            records[sessionId] = [];
          }
  
          records[sessionId].push(handRecord);
        } else {
          console.log(handRecord)
        }

        return records;
      }, {});

      const finalRes = {
        sessions: Object.keys(handResultsPerSession),
        records: handResultsPerSession 
      };

      setData(finalRes);
    }, {});
  }, [tournamentId]);

  const { records, sessions } = data;

  return (
    <Wrapper>
      <h1>Hand Exploiter</h1>
      <Paper>
        <div>
          <h2>Hands</h2>

          <div>
            {sessions.map((session) => (
              <Accordion key={session} className="section-accordion">
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>Session ID: {session}</Typography>
                </AccordionSummary>
                <AccordionDetails className="AccordionDetails">
                  <div>
                    {records[session].map((record) => {
                      const {
                        dealer,
                        board,
                        ns,
                        nh,
                        nd,
                        nc,
                        ss,
                        sh,
                        sd,
                        sc,
                        es,
                        eh,
                        ed,
                        ec,
                        ws,
                        wh,
                        wd,
                        wc,
                      } = record;
                      return (
                        <HandRecord key={`${session}-${board}`}>
                          <p><b>Board</b>: {board}</p>
                          <p><b>Delaer</b>: {dealer}</p>
                          <p><b>Board No</b>: {board}</p>
                          <p className="bold">
                            N - S {ns} / H {nh} / D {nd} / C {nc}
                          </p>
                          <p className="bold">
                            S - S {ss} / H {sh} / D {sd} / C {sc}
                          </p>
                          <p className="bold">
                            E - S {es} / H {eh} / D {ed} / C {ec}
                          </p>
                          <p className="bold">
                            W - S {ws} / H {wh} / D {wd} / C {wc}
                          </p>
                        </HandRecord>
                      );
                    })}
                  </div>
                </AccordionDetails>
              </Accordion>
            ))}
          </div>
        </div>
        <InputWrap>
          <TextField
            id="filled-hidden-label-small"
            label="Enter tournament ID"
            variant="filled"
            size="small"
            value={tournamentId}
            onChange={handleChange}
          />
          <Button variant="contained" onClick={fetch}>
            Go
          </Button>
        </InputWrap>
      </Paper>
    </Wrapper>
  );
}

export default App;
